	module.exports = {"data":{"markdownRemark":{"id":"/Users/cmarlow/Projects/lo-life/src/pages/rediscover-us.md absPath of file >>> MarkdownRemark","html":"<h2>Group intensive 6 week</h2>\n<p>As a group of 4-6 we will explore our inner goddess.\nWe will strengthen our connection with our authentic selves\nthrough expanding our knowledge on our thoughts and emotions\nto have a better understanding on how to bring purpose and joy\nback into our lives.</p>\n<p>What is included:</p>\n<ul>\n<li>Life assessment call (free assessment - 45min)</li>\n<li>The support of a female tribe going on the same journey as you</li>\n<li>6 weekly meetings via phone, Skype, Zoom etc)</li>\n<li>Unlimited Support in a private Facebook group with your tribe along w/ myself</li>\n<li>6 week methodology + homework and follow up</li>\n<li>weekly focus on specific segments of your life to help rediscover you</li>\n<li>Tools to help maintain progress and continue your journey after coaching</li>\n<li>Breathing &#x26; Meditation tools</li>\n</ul>\n<p>By the end of this program you will:</p>\n<ul>\n<li>Become more in tuned with yourself, which will lead to a more excited, inspired, passionate, &#x26; fulfilled life.</li>\n<li>Create a vision board, and build an environment/daily routine that supports the realization of your purpose.</li>\n<li>Allow yourself to open your heart to the universe and what is has to offer, allowing your heart to feel more peace, happiness, inspiration &#x26; desire.</li>\n<li>Connect deeper to your true inner goddess, &#x26; how you can better express your authentic self in your everyday life</li>\n<li>Be in love with your most authentic self</li>\n<li>Be on your way to building an unshakeable confidence</li>\n</ul>","frontmatter":{"date":"May 21, 2018","path":"/rediscover-us","title":"Rediscover Us"}}},"pathContext":{}}

